.blog-page{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
    margin-top: 2.5em;
  }
}

.blog-item{
  padding: 3.75em 0;
  &__inner{
    width: 89.125em;
    max-width: 100%;
    margin: 0 auto;
  }
  .blog-card__info{
    margin-top: .2em;
    &:before{
      display: none;
    }
  }
  &__top{
    margin-top: 1.19em;
    padding: 1.875em;
    background: #FBFBFB;
  }
}

.article{
  margin-top: 2.31em;
  &__text{
    margin-top: 4em;
  }
  &__images{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.24em / 3));
    gap: 2.12em;
    @include lastM(0 0 2.12em);
    img{
      display: block;
      width: 100%;
      min-height: 100%;
      object-fit: cover;
    }
  }
  .product-slider{
    @media screen and (min-width: 577px){
      &__container{
        width: calc(100% - 1px);
        margin-left: 0;
      }
      &__slide{
        width: calc(100% / 3);
      }
    }
    .swiper-nav{
      position: static;
      margin: 3.5em 0 0;
      justify-content: center;
    }
  }
}

.article-group{
  @include lastM(0 0 6.63em);
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.31em));
  gap: 2.62em;
  &:nth-child(even){
    .article-group__content{
      order: -1;
    }
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    align-self: center;
  }
}

.text{
  h4{
    font-size: 1.8125em;
    line-height: 120%;
    margin-bottom: .5em;
  }
  li,
  p{
    color: $darkGray;
    font-size: 1.0625em;
    line-height: 153%;
    @include lastM(0 0 1em);
  }
  ol {
    counter-reset: num;
    @include lastM(0 0 1.5em);
    li {
      position: relative;
      counter-increment: num;
      padding-left: 2.53em;
      &:before {
        content: counter(num) '. ';
        position: absolute;
        top: 0;
        left: 0;
        font-size: .8125em;
        line-height: 120%;
        width: 2.3em;
        height: 2.3em;
        @include center;
        background-color: $black;
        color: #fff;
        @include sameNumWidth;
      }
    }
  }
  blockquote{
    border: .625em solid #F9F9F9;
    padding: 1.25em;
    font-weight: 500;
    p{
      color: #000;
    }
  }
  hr{
    height: 2.12em;
    opacity: 0;
    display: block;
  }
  blockquote{
    @include lastM(0 0 1em);
  }
}

.share{
  display: flex;
  align-items: center;
  margin-top: 2.81em;
  &__items{
    margin-left: 2.12em;
    display: flex;
  }
  &__item{
    @include center;
    width: 3.4375em;
    height: 3.4375em;
    background: #F4F4F4;
    @include lastM(0 1.12em 0 0);
    svg{
      max-height: 1.1875em;
      max-width: 1.4375em;
      width: auto;
      height: auto;
    }
    &:hover{
      background: $black;
      path{
        fill: #fff;
      }
    }
  }
}

.blog-collection{
  padding: 2.5em 0;
  overflow: hidden;
  .text48{
    max-width: 70%;
  }
}


@media screen and (max-width: 992px){
  .blog-page{
    padding: 1.56em 0;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.875em / 3));
      gap: .9375em;
      margin-top: 1em;
    }
  }

  .blog-item{
    padding: 1.56em 0;
    .blog-card{
      &__info{
        margin-top: .625em;
        display: flex;
        &-split{
          display: block;
        }
      }
      &__date{
        color: #7D7D7D;
      }
    }
    &__top{
      padding: 1.56em;
      margin-top: 1.25em;
      .text19{
        font-size: 1.0625em;
        line-height: 170%;
      }
    }
  }

  .article{
    margin-top: 1.56em;
    &__text{
      margin-top: 1.56em;
    }
    &__images{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.875em / 3));
      gap: .9375em;
    }
    .product-slider{
      width: calc(100% + 3.125em);
      margin-left: -1.5625em;
      &__container{
        width: 100%;
      }
      .swiper-nav{
        margin: 1.25em 0 0;
      }
    }
  }

  .article-group{
    @include lastM(0 0 1.25em);
    grid-template-columns: 100%;
    gap: 1.56em;
  }

  .text{
    h4{
      font-size: 1.25em;
      line-height: 130%;
      margin-bottom: .75em;
    }
    p,li{
      font-size: 1em;
      line-height: 156%;
    }
    p{
      @include lastM(0 0 .94em);
    }
    li{
      @include lastM(0 0 1.25em);
    }
    hr{
      height: .6em;
    }
    blockquote{
      padding: .9375em;
    }
  }

  .share{
    margin-top: 1.56em;
  }

  .blog-collection{
    padding: 1.25em 0;
  }
}


@media screen and (max-width: 576px){
  .blog-page{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.56em;
    }
    .blog-card{
      &__info{
        display: flex;
        &:before{
          display: block;
        }
        &-split{
          display: block;
        }
      }
      &__date{
        color: #7D7D7D;
      }
    }
  }

  .article{
    &__images{
      grid-template-columns: 100%;
    }
    .product-slider{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
  }
}
