.promos{
  padding: 4.375em 0 6.875em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
    gap: 2.5em;
  }
}

.promo-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  &__top{
    display: block;
    position: relative;
    &:after{
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.34) 0%, rgba(0, 0, 0, 0.34) 100%);
      opacity: 0;
      transition: all .3s;
    }
    &:hover{
      color: #fff;
      &:after{
        opacity: 1;
      }
    }
  }
  &__img{
    @include imgCover(57.25%);
  }
  &__title{
    position: absolute;
    padding: 2.31em 1.81em;
    left: 0;
    bottom: 0;
    z-index: 2;
    max-width: 65%;
    @include truncate(3);
  }
  &__text{
    margin: .56em 0 .81em;
    font-size: 1em;
    line-height: 156%;
    color: #3C3C3C;
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .625em;
    border: 2px solid #F4F4F4;
  }
  &__info{
    .text15{
      line-height: 153%;
    }
  }
  &__date{
    color: #606060;
  }
  &__link{
    margin-left: .25em;
    flex-shrink: 0;
    padding: 0 .94em;
    height: 2.9375em;
    span{
      font-size: .9375em;
    }
  }
}

.promos-collection{
  padding: 3.125em 0;
  overflow: hidden;
}

.promos-slider{
  margin-top: 2.25em;
  .swiper{
    &-container{
      width: calc(100% + 2.5em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 25%;
      padding-right: 2.5em;
    }
    &-nav{
      justify-content: center;
      margin-top: 3.5em;
    }
  }
}

.promo-item{
  padding: 4.375em 0 3.125em;
  &__info{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}

.promo-banner{
  padding: 2.25em 3.75em;
  min-height: 28.4375em;
  position: relative;
  display: flex;
  align-items: flex-end;
  &__img{
    width: 100%;
    height: 100%;
    right: 0;
    top: 0;
    object-fit: cover;
    position: absolute;
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 26em;
    @include grid100gap(.75em);
  }
  &__desc{
    font-size: 1em;
    line-height: 156%;
    color: #3C3C3C;
  }
  &__nav{
    display: flex;
    margin-top: .25em;
    .m-btn{
      height: 3.875em;
      padding: 0 2em;
    }
  }
}

.info-card{
  border: .625em solid #F8F8F8;
  padding: 1.875em;
  &__text{
    color: #333;
    margin-top: .625em;
    p{
      @include lastM(0 0 .5em);
    }
    b{
      font-weight: 700;
    }
    a{
      text-decoration: underline;
      color: $primary;
    }
  }
}

.profile-sales{
  .promos__grid{
    margin-top: 2em;
    @media screen and (min-width: 993px){
      grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    }
  }
}


@media screen and (max-width: 992px){
  .promos{
    padding: 1.56em 0 5em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .promo-card{
    &__title{
      padding: 1.3em 1.25em;
      max-width: 100%;
      br{
        display: none;
      }
    }
    &__info{
      .text15{
        font-size: .875em;
        line-height: 150%;
      }
    }
    &__text{
      margin-bottom: .38em;
    }
  }

  .promos-collection{
    padding: 1.25em 0;
    .text30{
      text-align: center;
    }
  }

  .promos-slider{
    margin-top: 1.56em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: 50%;
        padding-right: 1.25em;
      }
      &-nav{
        margin-top: 2em;
      }
    }
  }

  .promo-item{
    padding: 1.56em 0 1.25em;
    &__info{
      margin-top: 1.56em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .promo-banner{
    padding: 1.875em;
    min-height: 17.6875em;
    overflow: hidden;
    &__content{
      max-width: 14.75em;
      gap: 1.25em;
    }
    &__desc{
      font-size: .9375em;
      line-height: 147%;
    }
    &__img{
      width: auto;
      right: -4em;
    }
    &__nav{
      margin: 0;
      .m-btn{
        padding: 0 1.12em;
        height: 3.0625em;
        span{
          font-size: 1em;
        }
      }
    }
  }

  .info-card{
    padding: .9375em;
  }

  .profile-sales{
    .promos__grid{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px) {
  .promos {
    &__grid {
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .promos-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .promo-item{
    &__info{
      grid-template-columns: 100%;
      gap: 1.56em;
    }
  }

  .promo-banner{
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    padding: 1.75em .9375em;
    min-height: 17.4375em;
    &__img{
      right: -9em;
    }
    &__desc{
      max-width: 14em;
    }
  }
}
