.shop{
  padding: 3.81em 0;
  &__top{
    margin-top: 2.875em;
    display: grid;
    grid-template-columns: 43.3125em calc(100% - 43.3125em - 2.5em);
    gap: 2.5em;
  }
  &__map{
    height: 34.9375em;
    width: 100%;
    min-height: 100%;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    margin-top: 2.56em;
    max-width: 43.3125em;
    .text17{
      line-height: 153%;
    }
  }
  &__nav{
    display: flex;
    margin-top: 3.19em;
  }
}

.shop-info{
  max-width: 43.3125em;
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 3.25em));
  gap: 2.5em 6.5em;
  &__item{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 20.25em;
  }
  &__title{
    color: #616161;
    font-size: .9375em;
    line-height: 133%;
  }
  &__link,
  &__desc{
    margin-top: .375em;
    .text17{
      line-height: 135%;
    }
  }
  &__link{
    font-weight: 500;
    line-height: 117%;
    .text17{
      line-height: 117%;
    }
  }
  &__site{
    text-decoration-line: underline;
    color: $primary;
  }
}

.shop-socials{
  display: flex;
  margin-top: 2.5em;
  &__item{
    padding: 0 1.56em;
    background: #FBFBFB;
    height: 2.875em;
    display: flex;
    align-items: center;
    @include lastM(0 .94em 0 0);
    &:hover{
      background: $black;
      color: #fff;
      path{
        fill: #fff;
      }
    }
    svg{
      max-width: 1.5em;
      max-height: 1em;
      height: auto;
      width: auto;
      margin-right: .625em;
    }
    span{
      font-size: .875em;
      line-height: 130%;
    }
  }
}

.back-link{
  display: flex;
  align-items: center;
  height: 2.875em;
  font-weight: 500;
  &:hover{
    color: $primary;
    circle{
      fill: $primary;
    }
    path{
      fill: #fff;
    }
  }
  span{
    font-size: 1.0625em;
    line-height: 120%;
  }
  svg{
    width: 1.5625em;
    height: auto;
    margin-right: .625em;
  }
}

.shops{
  padding: 1.875em 0;
  &__top{
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F6F6F6;
  }
  &__items{
    margin-top: 4.06em;
    @include grid100gap(2.5em);
  }
  &__grid{
    display: grid;
    grid-template-columns: 25.81em calc(100% - 25.81em - 1.62em);
    gap: 1.62em;
    margin-top: 3.56em;
  }
  .map{
    height: 38.8125em;
  }
  &__cards{
    height: 38.8125em;
    border: 1px solid #F6F6F6;
    padding-right: .31em;
    &-scroll{
      padding-right: .31em;
      overflow-y: auto;
      height: 100%;
      &::-webkit-scrollbar {
        width: 0.3125em;
        height: 0.3125em;
      }
      &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 0;
      }
      &::-webkit-scrollbar-thumb {
        background: rgba(#000,.2);
      }
    }
  }
}

.shops-nav,
.shops-city{
  display: flex;
  align-items: center;
  position: relative;
  &__title{
    font-size: 1em;
    line-height: 125%;
    margin-right: 1.19em;
  }
}

.shops-city{
  .header-city{
    position: static;
  }
}

.shops-nav{
  margin-left: 6.5em;
  &__links{
    display: flex;
  }
  &__link{
    height: 3.625em;
    display: flex;
    align-items: center;
    font-weight: 500;
    text-transform: uppercase;
    border-bottom: 2px solid transparent;
    @include lastM(0 1.25em 0 0);
    span{
      font-size: 0.875em;
      line-height: 143%;
    }
    &._active{
      font-weight: 900;
      border-color: $primary;
      pointer-events: none;
    }
  }
}

.shop-card{
  &._active{
    .shop-card__btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &__top{
    display: grid;
    grid-template-columns: 15.8125em calc(100% - 15.8125em - 2.44em);
    gap: 2.44em;
    align-items: flex-start;
  }
  &__media{
    padding: .375em;
    border: 1px solid #F6F6F6;
  }
  &__img{
    display: block;
    @include imgCover(56%);
  }
  &__content{
    padding-top: .375em;
  }
  &__items{
    display: grid;
    grid-template-columns: 12em 23em 16em 21em calc(100% - 76em);
    gap: 1em;
    margin-top: .875em;
  }
  &__item{
    max-width: 15.75em;
    &-title{
      font-size: 0.9375em;
      line-height: 133%;
      color: #616161;
    }
    &-desc{
      font-size: 1.0625em;
      line-height: 135%;
    }
  }
  &__btn{
    display: flex;
    align-items: center;
    color: $primary;
    font-weight: 500;
    &:hover{
      span{
        border-color: $primary;
      }
    }
    svg{
      width: .625em;
      height: auto;
      margin-right: .75em;
    }
    span{
      font-size: 1.0625em;
      line-height: 135%;
      border-bottom: 1px solid rgba($primary,.4);
      transition: all .3s;
    }
    ._view2{
      display: none;
    }
  }
  &__nav{
    display: flex;
    margin-top: .5em;
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $primary;
    &:hover{
      opacity: .9;
    }
    svg{
      width: .5em;
      height: auto;
      margin-left: .12em;
    }
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
  &__bottom{
    margin-top: 2.5em;
    display: none;
  }
  &__map{
    width: 100%;
    height: 38.8125em;
  }
}

.shop-card2{
  @include grid100gap(.5em);
  padding: 1.25em 1.75em;
  border-bottom: 1px solid #F6F6F6;
  border-right: 1px solid #F6F6F6;
  &._active{
    background: #FBFBFB;
    border-right-color: #FBFBFB;
  }
  &:last-child{
    border-bottom: none;
  }
  &__city{
    font-size: .9375em;
    line-height: 133%;
    color: #616161;
  }
  &__name{
    font-size: 1.125em;
    line-height: 111%;
    font-weight: 500;
    font-family: $font2;
  }
  &__time,
  &__phone,
  &__address{
    font-size: 1.0625em;
    line-height: 135%;
  }
  &__name,
  &__phone{
    display: inline-flex;
    margin-right: auto;
  }
  &__time{
    color: #A4A4A4;
  }
  &__nav{
    display: flex;
  }
}


@media screen and (max-width: 992px){
  .shop{
    padding: 1.25em 0;
    &__top{
      margin-top: .81em;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__map{
      height: auto;
    }
    &__text{
      margin-top: .75em;
      .text17{
        font-size: 1em;
        line-height: 150%;
      }
    }
    &__nav{
      margin-top: .75em;
    }
  }

  .shop-info{
    max-width: 100%;
    margin-top: 1.56em;
    grid-template-columns: repeat(2,calc(50% - 1.375em));
    gap: 1.75em 2.75em;
  }

  .shop-socials{
    margin-top: 1.75em;
  }

  .shops-city{
    .header-city{
      &__hidden{
        width: 100%;
        left: 0;
      }
    }
  }

  .shops{
    padding: 0 0 1.875em;
    &__top{
      justify-content: space-between;
    }
    &__grid{
      grid-template-columns: 15em calc(100% - 15.31em);
      gap: .31em;
      margin-top: 3em;
    }
    .map{
      height: 42.4375em;
    }
    &__cards{
      height: 42.4375em;
      border: none;
      position: relative;
      &-scroll{
        &::-webkit-scrollbar-thumb {
          border-radius: 56.25em;
        }
      }
      &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 5.5625em;
        bottom: -3.31em;
        left: 0;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 50.51%);
      }
    }
  }

  .shop-card{
    &__top{
      grid-template-columns: 12.5625em calc(100% - 12.5625em - .94em);
      gap: .94em;
    }
    &__media{
      padding: 0;
    }
    &__content{
      padding: 0;
      position: relative;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1.19em));
      gap: .75em 2.38em;
    }
    &__item{
      width: 100%;
    }
    &__title{
      font-size: 1.1875em;
      line-height: 105%;
    }
    &__btn{
      &-wrap{
        grid-column: 1/-1;
      }
      span{
        line-height: 117%;
      }
    }
    &__nav{
      position: absolute;
      bottom: 0;
      right: 0;
      width: calc(50% - 1.19em);
    }
    &__map{
      height: 27.125em;
    }
  }

  .shop-card2{
    border-right: none;
    padding: 1.25em .5em 1.25em .94em;
    &__name{
      font-size: 1.0625em;
      line-height: 117%;
    }
    &__time,
    &__phone,
    &__address{
      font-size: 1em;
      line-height: 125%;
    }
  }
}


@media screen and (max-width: 576px){
  .shop{
    &__top{
      grid-template-columns: 100%;
    }
    &__map{
      height: 22.6875em;
    }
    &__text{
      margin-top: 1.25em;
    }
    &__nav{
      margin-top: .94em;
    }
  }

  .shop-info{
    margin-top: 1.44em;
    grid-template-columns: 100%;
  }

  .shops{
    padding-top: .5em;
    &__top{
      display: block;
    }
    &__items{
      gap: 1.56em;
      margin-top: 1.56em;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.44em;
      gap: 0;
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
    &__cards{
      height: 25.875em;
      &:before{
        bottom: 0;
      }
    }
    .map{
      height: 38.8125em;
    }
  }

  .shops-nav{
    margin: 0;
  }

  .shop-card{
    &__top{
      grid-template-columns: 100%;
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
    &__content{
      padding: 1.75em .9375em .9375em;
      border-bottom: 1px solid #F6F6F6;
    }
    &__title{
      font-size: 1.125em;
    }
    &__items{
      margin-top: .5em;
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__nav{
      width: auto;
      right: .9375em;
      bottom: .9375em;
    }
    &__item{
      max-width: 100%;
      &-title{
        display: none;
      }
      &-desc{
        margin-top: 0;
      }
      &._city{
        position: absolute;
        top: 0;
        left: .9375em;
        width: 100%;
        .shop-card__item-desc{
          font-size: .9375em;
          color: #616161;
        }
      }
      &._time{
        .shop-card__item-desc{
          color: #A4A4A4;
        }
      }
    }
    &__media{
      padding: .375em 3.75em;
    }
  }
}
