.claims-table{
  @media screen and (min-width: 993px){
    .orders-table__grid{
      grid-template-columns: 15em 12.5em 18.5em calc(100% - 39em);
    }
  }
  .orders-table__grid{
    border: none;
  }
  .order-row{
    background-color: transparent;
  }
}

.new-claim{
  margin-top: 3.25em;
  &__grid{
    margin-top: 2em;
    padding: 2em 2.5em 3.62em;
    border: 1px solid #F0F0F0;
    display: grid;
    grid-template-columns: 38.25em calc(100% - 38.25em - 5.62em);
    justify-content: space-between;
    min-height: 33.875em;
    align-items: flex-start;
  }
  &__hint{
    padding-top: 2.38em;
    color: #808080;
    p{
      font-size: .875em;
      line-height: 143%;
      @include lastM(0 0 1.25em);
    }
    a{
      text-decoration: underline;
    }
  }
  .success-form{
    padding-top: 5em;
    padding-bottom: 5em;
    max-width: 28.5em;
  }
  &__content{
    padding-top: 2.38em;
    .new-claim__hint{
      padding-top: 1.8em;
    }
  }
  &__docs{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  .download-link{
    @include lastM(0 0 1.94em);
  }
  &__bottom{
    margin-top: 2.44em;
    display: flex;
  }
}

.download-link{
  display: flex;
  align-items: center;
  font-weight: 500;
  color: $primary;
  &:hover{
    opacity: .9;
  }
  &._border{
    padding: 0 .94em;
    height: 2.9375em;
    border: 1px solid $black;
    color: $black;
    &:hover{
      opacity: 1;
      border-color: $primary;
      color: $primary;
    }
  }
  span{
    font-size: 1em;
    line-height: 169%;
  }
  img{
    width: 1.5625em;
    height: auto;
    margin-left: .625em;
  }
}

.claim-form{
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(1.875em);
  }
}

.file-input2{
  input:hover{
    &~.file-input2__inner{
      border-color: $black;
    }
  }
  &__inner{
    border: 1px dashed #E8E8E8;
    background: #F9F9F9;
    height: 2.625em;
    color: $darkGray;
    @include center;
    img{
      width: .8125em;
      flex-shrink: 0;
      margin-right: .69em;
    }
  }
}

.claims{
  .tabs{
    margin-top: 1.875em;
  }
  @media screen and (min-width: 993px){
    .claims-table{
      .orders-table__grid{
        grid-template-columns: 16em 22em calc(100% - 40em);
      }
    }
  }
}

.tab3{
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  color: #575757;
  font-weight: 700;
  text-transform: uppercase;
  transition: all .3s;
  position: relative;
  @include lastM(0 3.81em 0 0);
  &:not(:last-child):after{
    content: '';
    position: absolute;
    top: 50%;
    left: 100%;
    margin-left: 1.905em;
    transform: translate(-50%,-50%);
    width: 1px;
    height: 0.8125em;
    background: #D9D9D9;
  }
  span{
    font-size: .9375em;
    line-height: 200%;
  }
  &:hover{
    cursor: pointer;
    color: #282828;
  }
  &._active{
    color: #282828;
    border-bottom-color: $primary;
  }
}


@media screen and (max-width: 992px){
  .claims-table{
    .orders-table__head{
      display: none;
    }
    .orders-table__rows{
      @include grid100gap(.94em);
    }
    .order-row{
      grid-template-columns: 100%;
      gap: .31em;
      border: 1px solid #EBEBEB;
      padding: .94em;
      &__col{
        padding: 0 !important;
        min-height: 1.875em;
        justify-content: space-between;
      }
      &__title{
        display: block;
        font-size: .875em;
        line-height: 130%;
        font-weight: 500;
      }
    }
    ._full{
      .order-row__title{
        flex-shrink: 0;
      }
      .order-row__desc{
        text-align: right;
      }
    }
  }

  .new-claim{
    margin-top: 2.5em;
    &__grid{
      border: none;
      padding: .94em;
      margin-top: .94em;
      grid-template-columns: 23.375em calc(100% - 23.375em - 1.56em);
      gap: 1.56em;
      min-height: unset;
      &._type2{
        grid-template-columns: 100%;
        .success-form{
          padding-top: 2.5em;
          padding-bottom: 2.5em;
        }
      }
    }
    &__hint{
      padding-top: 2.6em;
    }
    .success-form{
      max-width: 16em;
      gap: .625em;
    }
    &__content{
      padding-top: 0;
      .new-claim__hint{
        padding-top: .625em;
      }
    }
    &__docs{
      .download-link{
        @include lastM(0 0 .625em);
      }
    }
    &__bottom{
      margin-top: .625em;
    }
  }

  .claim-form{
    &__fields{
      gap: 1.5em;
    }
  }

  .claims {
    .tabs {
      margin-top: .9375em;
      margin-right: 0;
    }
  }

  .tab3{
    @include lastM(0 1.94em 0 0);
    &:not(:last-child):after{
      margin-left: .97em;
    }
    span{
      font-size: .8125em;
      line-height: 230%;
    }
  }
}


@media screen and (max-width: 576px){
  .new-claim{
    &__grid{
      grid-template-columns: 100%;
    }
    &__hint{
      padding: 0;
    }
  }
}
