.favourite-nav{
  position: sticky;
  top: 1em;
}

.favourite-link{
  min-height: 4.25em;
  padding: 1em 2.5em;
  display: flex;
  align-items: center;
  background: #FBFBFB;
  color: $darkGray;
  &:hover{
    background: #FFF;
    color: $black;
  }
  &._active{
    background: #FFF;
    color: $black;
  }
  &._all{
    color: $black;
  }
  &__title{
    font-size: 1.125em;
    font-weight: 500;
    line-height: 156%;
  }
  &__num{
    color: #A6A6A6;
    font-size: .75em;
    line-height: 233%;
    margin: -.5em 0 0 .75em;
  }
}

.cart-empty{
  padding: 4.5em 0 5em;
  &__inner{
    @include grid100gap(1em);
    margin: 0 auto;
    max-width: 100%;
    width: 32em;
    text-align: center;
  }
  &__img{
    width: 8.1875em;
    margin: 0 auto 2em;
    position: relative;
    &._small{
      max-width: 6em;
    }
    img{
      width: 100%;
      display: block;
    }
    .lk-link__count{
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(1.19em,-50%);
      width: 2.9375em;
      height: 2.9375em;
      span{
        font-size: 1.3125em;
      }
    }
  }
  .text17{
    color: #4C4C4C;
    a{
      font-weight: 700;
    }
  }
  &__nav{
    margin-top: .625em;
    display: flex;
    justify-content: center;
    .m-btn{
      height: 3.75em;
      padding: 0 2.25em;
      span{
        font-size: 1.125em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .favourite{
    .catalog__sidebar{
      display: block;
      order: -1;
    }
  }

  .favourite-nav{
    display: flex;
    flex-wrap: wrap;
  }

  .favourite-link{
    margin: 0 .5em .5em 0;
    min-height: unset;
    height: 2.75em;
    padding: 0 .625em;
    background: #FAFAFA;
    &._active,
    &:hover{
      background: #FAFAFA;
    }
    &__title{
      font-size: .875em;
    }
    &__num{
      margin-left: .25em;
    }
    &._all{
      border: 1px solid #EEE;
      background: transparent;
    }
  }

  .cart-empty{
    .text17{
      font-size: 1em;
      line-height: 156%;
    }
    &__nav{
      margin-top: 0;
    }
    &__img{
      margin-bottom: 1.12em;
    }
  }
}
