.profile{
  padding: 3.75em 0 7.5em;
  &__grid{
    display: grid;
    grid-template-columns: 24.875em calc(100% - 24.875em - 2.625em);
    gap: 2.625em;
  }
  .sort{
    display: none;
  }
  .promos-collection{
    padding: 0;
    margin-top: 6em;
  }
  @media screen and (min-width: 993px){
    .promos-slider{
      .swiper-slide{
        width: calc(100% / 3);
      }
    }
  }
}

.lk-menu{
  position: sticky;
  top: 1em;
  &__bg,
  &__close{
    display: none;
  }
}

.lk-nav{
  overflow: hidden;
  background: #FFF;
  border: 1px solid #F0EFED;
}

.lk-link{
  min-height: 4.625em;
  display: flex;
  align-items: center;
  padding: .75em 2.5em .75em 3.125em;
  color: #232323;
  &._active,
  &:hover{
    background-color: #F2F2F2;
    color: $black;
  }
  &__icon{
    width: 1.4375em;
    flex-shrink: 0;
    margin-right: .625em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 125%;
  }
  &__count{
    position: static;
    margin-left: auto;
    transform: unset;
    width: 1.6875em;
    height: 1.6875em;
    border: none;
    span{
      font-size: .75em;
    }
  }
}

.lk-buttons{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
  gap: 2.5em;
}

.lk-btn{
  min-height: 9.375em;
  position: relative;
  padding: 1.25em .25em;
  border: 1px solid #EAEAEA;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  .lk-link__count{
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(50%,-50%);
  }
  &__icon{
    height: 2em;
    flex-shrink: 0;
    @include center;
    margin-bottom: 1.25em;
    svg{
      max-width: 100%;
      max-height: 100%;
      width: auto;
      height: auto;
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 147%;
    font-weight: 500;
    text-transform: uppercase;
  }
  &:hover,
  &._active{
    background-color: #F2F2F2;
    border-color: #F2F2F2;
    color: $black;
  }
}

.current-orders{
  padding: 1.69em 2.5em;
  border: 1px solid #E9EAEE;
  margin-top: 3.125em;
  .info-text{
    margin-top: 2.13em;
  }
  .orders-table{
    margin-top: .875em;
  }
}

.info-text{
  display: flex;
  align-items: center;
  color: #767676;
  .text16{
    line-height: 150%;
  }
  img{
    width: 1.75em;
    flex-shrink: 0;
    margin-right: 1.56em;
  }
}

.profile-top{
  margin-bottom: 2.5em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
  gap: 2.5em;
  &__min{
    border: .4375em solid #E9EAEE;
    padding: 2em;
    display: flex;
    align-items: center;
    flex-direction: column;
    color: $primary;
    text-align: center;
    img{
      width: 1.875em;
      margin-bottom: 1.12em;
    }
    .text20{
      line-height: 110%;
    }
  }
}

.manager{
  padding: 1.56em 2.5em;
  background: #FBFBFB;
  grid-column: 2/-1;
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: .94em;
  }
  &__phone,
  &__email{
    display: flex;
    align-items: center;
    svg{
      width: 1.3125em;
      height: auto;
      margin-right: .94em;
      flex-shrink: 0;
    }
  }
  &__email{
    color: $primary;
    &:hover{
      span{
        border-color: $primary;
      }
    }
    span{
      font-size: 1em;
      line-height: 125%;
      transition: inherit;
      border-bottom: 1px solid rgba($primary,.3);
    }
  }
  &__phone{
    font-weight: 500;
    color: #434343;
    svg{
      width: 1.1875em;
    }
    span{
      font-size: 1.0625em;
    }
  }
}

.manager-info{
  display: flex;
  align-items: center;
  max-width: 14em;
  .avatar{
    margin-right: .81em;
  }
  .text18{
    line-height: 133%;
  }
}

.avatar{
  width: 4.375em;
  height: 4.375em;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 50%;
  }
}

.promos-table{
  margin-top: 2.5em;
  padding: 1.69em 2.5em 2.5em;
  border: 1px solid #E9EAEE;
  .m-table{
    margin-top: .875em;
  }
}

.m-table{
  color: #333;
  th,td{
    border-bottom: 1px solid #EBEBEB;
    padding: 0 1.19em;
    &:nth-child(2){
      @media screen and (min-width: 993px){
        width: 28em;
      }
    }
  }
  td{
    height: 4.125em;
    span{
      font-size: .875em;
      line-height: 130%;
      letter-spacing: 0.00875em;
    }
  }
  tbody{
    tr{
      &:nth-child(odd){
        background: #FBFBFB;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .profile{
    padding: 1.25em 0 5em;
    //position: relative;
    //z-index: 3;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__sidebar{
      position: relative;
      z-index: 3;
    }
    .menu-btn{
      margin-bottom: 1.875em;
    }
    .sort{
      display: flex;
      margin-top: 1.25em;
      &__title{
        display: block;
      }
      .fg{
        width: 20.625em;
        max-width: 100%;
        margin-left: 1.12em;
      }
      .nice-select{
        height: 3.125em;
        .current{
          font-weight: 700;
          font-size: .875em;
          text-transform: uppercase;
          color: $black;
        }
        .list{
          max-height: unset;
        }
      }
    }
    .promos-collection{
      margin-top: 2.5em;
    }
  }

  .lk-menu{
    @include menu;
    padding: 3.125em 0;
    &._open{
      transform: translateX(0);
      &~.lk-menu__bg,
      &~.lk-menu__close{
        display: block;
      }
    }
  }

  .lk-link{
    min-height: 4.125em;
    padding-left: 1.56em;
    padding-right: 1.56em;
  }

  .lk-buttons{
    margin-top: 1.25em;
    grid-template-columns: repeat(3,calc(100% / 3 - .625em));
    gap: .9375em;
  }

  .lk-btn{
    min-height: 6.6875em;
    padding: 1em .125em;
    &__icon{
      margin-bottom: 1em;
    }
    &__title{
      font-size: .875em;
    }
  }

  .current-orders{
    margin-top: 1.875em;
    padding: 1.56em;
    text-align: center;
    &._type2{
      padding: 0;
      border: none;
      text-align: left;
    }
    .info-text{
      margin: 1.875em auto 0;
      max-width: 33.25em;
    }
    .orders-table{
      margin-top: .9375em;
    }
  }

  .info-text{
    flex-direction: column;
    text-align: center;
    img{
      margin: 0 0 1.56em;
    }
  }

  .profile-top{
    margin-bottom: 1.56em;
    grid-template-columns: 100%;
    gap: .94em;
    &__min{
      padding: .875em;
      br{
        display: none;
      }
    }
  }

  .manager{
    grid-column: 1/-1;
    padding: 1.56em;
  }

  .promos-table{
    margin-top: 1.56em;
    padding: 0;
    border: none;
  }
}


@media screen and (max-width: 576px){
  .profile{
    .sort{
      &__title{
        display: none;
      }
      .fg{
        margin: 0;
        width: 100%;
      }
    }
  }

  .lk-buttons{
    grid-template-columns: repeat(2,calc(50% - .9375em / 2));
  }

  .manager{
    &__wrap{
      flex-direction: column;
      align-items: flex-start;
    }
    &__phone,
    &__email{
      margin-top: 1.56em;
    }
  }

  .promos-table{
    .m-table{
      margin-top: 1.56em;
    }
    thead{
      display: none;
    }
    tbody,
    table{
      display: block;
    }
    tr{
      display: block;
      padding: 1.12em 1.19em;
      border-bottom: 1px solid #EBEBEB;
    }
    td{
      display: block;
      height: unset;
      padding: 0;
      border: none;
      &:nth-child(even){
        font-weight: 700;
      }
      span{
        line-height: 178%;
      }
    }
  }

  .current-orders{
    .orders-table{
      margin-top: 1.25em;
    }
  }
}
