.vacancies {
  padding: 3.75em 0 3.125em;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: calc(100% - 27.5em);
    display: grid;
    grid-auto-columns: 100%;
    gap: 2.1875em;
  }
  &__right {
    width: 25em;
    margin-left: 2.5em;
  }
  &__text {
    li,
    p {
      font-size: 1.0625em;
      line-height: 140%;
    }
    ul {
      display: grid;
      grid-auto-columns: 100%;
      gap: 0.625em;
      li {
        position: relative;
        padding-left: 1.125em;
        &:before {
          content: '';
          position: absolute;
          top: 0.375em;
          left: 0;
          width: 0.5625em;
          height: 0.375em;
          background-image: url(../img/check5.svg);
          background-size: 100% auto;
          background-repeat: no-repeat;
          background-position: center;
        }
      }
    }
    p,ul{
      @include lastM(0 0 1em);
    }
    h3{
      margin-bottom: .9em;
      font-size: 1.3125em;
      line-height: 123%;
      font-weight: 700;
    }
    hr{
      height: 1.25em;
      opacity: 0;
      display: block;
    }
  }
  &-phone {
    background: #FBFBFB;
    padding: 2.5em;
    position: sticky;
    top: 1em;
    &__subtitle {
      margin-top: 0.625em;
    }
    &__items {
      display: grid;
      grid-auto-columns: 100%;
      gap: 0.9375em;
      margin-top: 1.875em;
    }
    &__item {
      display: flex;
      align-items: center;
      &:hover {
        i svg path {
          stroke: #E31E24;
        }
        span {
          color: #E31E24;
        }
      }
      i {
        flex-shrink: 0;
        width: 1.0625em;
        margin-right: 1.0625em;
      }
      i svg {
        width: 100%;
        height: auto;
      }
      i svg path {
        transition: all 125ms;
      }
    }
    .mail {
      span {
        color: #E31E24;
        text-decoration: underline;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .text27-tablet {
    font-size: 1.6875em;
  }
  .vacancies {
    padding: 1.875em 0;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      gap: 1.25em;
    }
    &__right {
      width: 100%;
      margin: 1.875em 0 0 0;
    }
    &__text {
      p,li{
        font-size: 1em;
      }
    }
    @media screen and (min-width: 577px){
      &__text{
        ul{
          &:nth-last-child(1),
          &:nth-last-child(2){
            max-width: calc(100% - 21em);
          }
        }
      }
    }
  }

  .vacancies-phone{
    @media screen and (min-width: 577px){
      width: 20.625em;
      margin-left: auto;
      margin-top: -18em;
    }
  }
}
