.vacancy-main {
  padding: 3.75em 0 6.25em;
  overflow-x: hidden;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__sidebar {
    width: 25em;
    margin-right: 2.5em;
  }
  &__content {
    width: calc(100% - 27.5em);
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
  }
  &__link {
    height: 3.125em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FAFAFA;
    border: 0.0625em solid transparent;
    padding: 0.3125em 1.5625em;
    margin-bottom: 0.625em;
    &:not(:last-child) {
      margin-right: 0.625em;
    }
    &--active {
      background: #FFF;
      border: 1px solid #EEE;
      font-weight: 700;
    }
    &:hover {
      color: #171717;
      background: #FFF;
      border: 1px solid #EEE;
    }
  }
  &__items {
    display: grid;
    grid-auto-columns: 100%;
    margin-top: 1.875em;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.875em 0;
    border-bottom: 0.0625em solid #F2F2F2;
    &-desc {
      margin-right: 2em;
    }
    &-wrap {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-top: 1.125em;
    }
    &-price {
      position: relative;
      margin-right: 2.5em;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: - 1.25em;
        transform: translate(0, -50%);
        width: 0.375em;
        height: 0.375em;
        background: #E31E24;
        border-radius: 50%;
      }
    }
    &-address {
      position: relative;
      color: #707070;
      margin-right: 2.5em;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: - 1.25em;
        transform: translate(0, -50%);
        width: 0.375em;
        height: 0.375em;
        background: #E31E24;
        border-radius: 50%;
      }
    }
    &-date {
      position: relative;
      color: #707070;
    }
    &-btn {
      height: 3.125em;
      padding: 0 3.3125em;
    }
  }
  &__categories {
    display: none;
    justify-content: center;
    align-items: center;
    border-top: 0.0625em solid #D4D4D4;
    border-bottom: 0.0625em solid #D4D4D4;
    padding: 0.9375em 0;
    cursor: pointer;
    svg {
      flex-shrink: 0;
      width: 1em;
      height: auto;
    }
    span {
      margin-left: 0.625em;
    }
  }
  &__bg {
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: #171717;
    opacity: 0.9;
    display: none;
    &--active {
      display: block;
    }
  }
}
.vacancy-sidebar {
  &__items {
    display: grid;
    grid-template-columns: 100%;
    border: 0.0625em solid #F0F0F0;
  }
  &__item {
    padding: 1.875em 2.5em;
    &:not(:last-child) {
      border-bottom: 0.0625em solid #F0F0F0;
    }
    &--active {
      color: #171717;
      background: #FBFBFB;
    }
    &:hover {
      color: #171717;
      background: #FBFBFB;
      .vacancy-sidebar__item-title {
        color: #171717;
      }
      .vacancy-sidebar__item-text {
        color: #4C4C4C;
      }
    }
  }
  &__close {
    display: none;
  }
  //&__bg {
  //  position: fixed;
  //  z-index: 2;
  //  top: 0;
  //  left: 0;
  //  right: 0;
  //  bottom: 0;
  //  width: 100%;
  //  height: 100%;
  //  background: #171717;
  //  opacity: 0.9;
  //  display: none;
  //  &--active {
  //    display: block;
  //  }
  //}
}

.vacancy-filter{
  &__bg,
  &__close{
    display: none;
  }
}

@media screen and (max-width: 992px) {
  .vacancy-filter{
    @include menu;
    padding: 3.125em 0;
    &._open{
      transform: translateX(0);
      &~.vacancy-filter__bg,
      &~.vacancy-filter__close{
        display: block;
      }
    }
  }

  ._vacancy-sidebar-open{
    .header{
      z-index: 0;
    }
  }

  .vacancy-main {
    padding: 1.25em 0 1.875em;
    &__inner {
      display: block;
    }
    &__sidebar {
      position: relative;
      z-index: 2;
    }
    &__content {
      width: 100%;
    }
    &__categories {
      width: calc(100% + 3.125em);
      display: flex;
      margin-left: -1.5625em;
    }
    &__links {
      margin-top: 1.25em;
    }
    &__link {
      height: 2.75em;
      padding: 0 0.625em;
    }
    &__item {
      display: block;
      &-desc {
        margin: 0 0 1.125em;
      }
      &-btn {
        display: inline-flex;
        padding: 0 4.125em;
      }
    }

  }
  .vacancy-sidebar {
    &__close {
      position: fixed;
      top: 0.8125em;
      right: 0.8125em;
      display: block;
      width: 0.9375em;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &__items {
      border-left: unset;
      border-right: unset;
    }
    &__item {
      padding: 0.9375em 1.5625em;
    }
  }
}

@media screen and (max-width: 576px) {
  .vacancy-main {
    &__item {
      padding: 1.25em 0;
      &-wrap {
        margin-top: 0.9375em;
      }
      &-price {
        margin-right: 0;
        margin-bottom: 0.3125em;
        &:after {
          display: none;
        }
      }
      &-address {
        margin-right: 0;
        margin-bottom: 0.3125em;
        &:after {
          display: none;
        }
      }
      &-btn {
        width: 100%;
      }
    }
  }
}
