.login{
  padding: 7.5em 0;
}

.login-form,
.reg-form{
  max-width: 100%;
  margin: 0 auto;
  padding: 2.5em;
  border: .625em solid #F9F9F9;
}

.login-form{
  width: 35.4375em;
  @include grid100gap(1.875em);
  &__nav{
    display: flex;
    justify-content: center;
    .m-btn{
      height: 3.75em;
      span{
        font-size: 1.125em;
      }
    }
    &._type2{
      display: grid;
      grid-template-columns: 11.875em calc(100% - 12.875em);
      gap: .9375em 1em;
      .m-btn{
        padding: 0 1em;
      }
    }
  }
  &__hint{
    text-align: center;
    a,
    button{
      text-decoration-line: underline;
      color: $primary;
      &:hover{
        text-decoration-color: transparent;
      }
    }
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    font-size: 1em;
    line-height: 125%;
    text-decoration: underline;
  }
  &__text{
    @include grid100gap(1em);
    .text16{
      line-height: 169%;
    }
  }
}

.reg-form{
  width: 89.125em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5625em));
    gap: 3.125em;
    .profile-edit__group{
      @include lastM(0 0 3.125em);
    }
  }
  .login-form__nav{
    justify-content: unset;
    margin-top: 3.125em;
  }
}


@media screen and (max-width: 992px){
  .login{
    padding: 2.5em 0 5em;
  }

  .login-form{
    width: 28.75em;
    padding: .9375em;
    &__nav{
      .m-btn{
        width: 100%;
      }
      &._type2{
        grid-template-columns: 100%;
      }
    }
    .m-check{
      label{
        padding-top: 0;
        span{
          font-size: .875em;
        }
      }
    }
    &__text{
      gap: 1.75em;
      &._type2{
        text-align: center;
      }
      .text30{
        text-align: center;
      }
    }
  }

  .reg-form {
    padding: .9375em;
    .text30{
      font-size: 1.6875em;
    }
    &__grid {
      grid-template-columns: 100%;
      gap: 1.875em;
      @media screen and (min-width: 577px){
        .profile-edit__group{
          grid-template-columns: repeat(2,calc(50% - .78em));
          gap: 1.56em;
          &>*:not(._small){
            grid-column: 1/-1;
          }
        }
      }
    }
  }
}
