.material-advantages{
  background: #FBFBFB;
  &__inner{
    display: grid;
    grid-template-columns: 55em calc(100% - 57.81em);
    gap: 2.81em;
  }
  &__content{
    padding: 4em 0;
  }
  .text56{
    max-width: 10em;
  }
  &__items{
    margin-top: 4.5em;
    display: grid;
    grid-template-columns: 1fr .95fr;
    gap: 3em 3.5em;
  }
  &__img{
    img{
      width: calc(100% + 6.125em);
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .material-advantages{
    padding: 1.875em 0 9.31em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      padding:0;
    }
    &__img{
      display: none;
    }
    .text56{
      max-width: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.5em;
    }
    .advantage-item{
      &:before{
        width: 2.4375em;
        height: 2.4375em;
        margin-right: .94em;
      }
      .text17{
        font-size: 1em;
        line-height: 162%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .material-advantages{
    padding-bottom: 0;
    &__inner{
      gap: 1.875em;
    }
    &__items{
      grid-template-columns: 100%;
    }
    &__img{
      display: block;
      img{
        width: calc(100% + 1.875em);
        margin-right: -.9375em;
      }
    }
  }
}
