.orders{
  padding: 1.63em 2.5em 2.81em;
  border: 1px solid #E9EAEE;
  margin-top: 2.5em;
  .pagination{
    margin-top: 2.81em;
    &__items{
      justify-content: unset;
    }
  }
}

.orders-table{
  &__grid{
    display: grid;
    grid-template-columns: 19em calc(100% - 56.5em) 23em 11.5em;
    gap: 1em;
  }
  &__head{
    border-bottom: 1px solid #EBEBEB;
    &-col{
      min-height: 2.75em;
      display: flex;
      align-items: center;
      letter-spacing: 0.00875em;
      &:first-child{
        padding-left: 1.19em;
      }
    }
    .text14{
      text-transform: uppercase;
      font-weight: 700;
    }
  }
  .text14{
    letter-spacing: 0.00875em;
  }
}

.order-row{
  position: relative;
  border-bottom: 1px solid #EBEBEB;
  &__link{
    @media screen and (min-width: 577px){
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }
  &:nth-child(odd){
    background: #FBFBFB;
  }
  &__col{
    min-height: 4.125em;
    display: flex;
    align-items: center;
    &:first-child{
      padding-left: 1.19em;
    }
  }
  &__title{
    display: none;
  }
  &__price{
    font-size: 1.25em;
    line-height: 120%;
    text-transform: uppercase;
  }
}

.status-select{
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    pointer-events: none;
    svg{
      width: .5em;
      height: auto;
      margin-left: .5em;
      margin-top: -.25em;
    }
  }
  .fg{
    position: absolute;
    top: -.94em;
    left: -1.56em;
    min-width: 17.4375em;
  }
  .nice-select{
    background: transparent !important;
    border: none;
    .current{
      display: none;
    }
    .list{
      box-shadow: 0 .25em 1.5625em 0 rgba(0, 0, 0, 0.07);
      border: none;
      margin-top: 0;
      //padding-top: 3.75em;
      max-height: unset;
    }
    .option{
      font-size: .875em;
      padding: 0 1.78em;
      height: 3.57em;
    }
  }

}

.order-status{
  display: flex;
  align-items: center;
  &__round{
    width: 0.5em;
    height: 0.5em;
    flex-shrink: 0;
    border-radius: 50%;
    margin-right: .5em;
  }
}

.order-item{
  margin-top: 2.5em;
  .address-card{
    margin-top: 3.125em;
  }
}

.order-detail{
  border: 1px solid #F0F0F0;
}

.order-info{
  padding: 1.25em 2.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #F0F0F0;
  &__wrap{
    display: flex;
    align-items: center;
  }
  &__status-text,
  &__date{
    color: #646464;
    font-size: 1em;
    line-height: 187.5%;
    b{
      font-weight: 700;
      color: $black;
    }
  }
  &__status{
    display: flex;
    align-items: center;
    &-badge{
      display: flex;
      align-items: center;
      padding: 0 .81em;
      border: 1px solid #F4F4F4;
      height: 2.3125em;
      font-weight: 700;
      margin-left: .5em;
      span{
        font-size: .875em;
        line-height: 130%;
      }
    }
  }
  &__nav{
    margin-left: .5em;
    flex-shrink: 0;
  }
  &__date{
    margin-right: .3125em;
  }
}

.back-link2{
  display: flex;
  align-items: center;
  background: #FBFBFB;
  height: 2.625em;
  padding: 0 1.19em;
  color: #000;
  &:hover{
    background: #000;
    color: #fff;
    path{
      stroke: #fff;
    }
  }
  span{
    font-size: 0.9375em;
    line-height: 130%;
  }
  svg{
    width: 1.0625em;
    height: auto;
    margin-right: .94em;
  }
}

.m-table,
.order-props{
  table{
    width: 100%;
  }
  th{
    text-transform: uppercase;
    text-align: left;
    height: 2.75em;
    font-weight: 700;
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
}

.order-props{
  padding: 2.19em 2.5em;
  th,td{
    padding: 0 1.19em;
    &:nth-child(2){
      width: 12em;
    }
  }
}

.order-prop{
  td{
    padding-top: 1.12em;
    padding-bottom: 1.12em;
    border-top: 1px solid #EBEBEB;
    height: 4.125em;
  }
  &__num{
    color: #888;
    font-size: .875em;
  }
  &__price{
    white-space: nowrap;
  }
}


@media screen and (max-width: 992px){
  .orders{
    padding: 0;
    border: none;
    margin-top: 1.25em;
    .pagination{
      margin-top: 1.56em;
    }
  }

  .orders-table{
    &__grid{
      grid-template-columns: 8em 10em calc(100% - 28em) 7em;
      gap: 1em;
    }
  }

  .order-item{
    margin-top: 1.25em;
    .address-card{
      margin-top: 2.5em;
    }
  }

  .order-info{
    padding: .9375em;
    align-items: flex-start;
    &__wrap{
      flex-wrap: wrap;
    }
    &__status,
    &__date{
      margin-top: .13em;
    }
  }

  .order-props{
    padding: .94em;
    th,td{
      padding-left: 0;
      padding-right: 0;
      &:nth-child(2){
        text-align: right;
        width: auto;
      }
    }
  }

  .status-select{
    &__show svg,
    .fg{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .orders{
    .pagination{
      &__items{
        justify-content: center;
      }
    }
  }

  .orders-table{
    &__head{
      display: none;
    }
    &__rows{
      @include grid100gap(.94em);
    }
  }

  .order-row{
    grid-template-columns: 100%;
    gap: .31em;
    border: 1px solid #EBEBEB;
    padding: .94em;
    &__col{
      padding: 0 !important;
      min-height: 1.875em;
      justify-content: space-between;
    }
    &__title{
      display: block;
      font-size: .875em;
      line-height: 130%;
      font-weight: 500;
    }
    &__link{
      border: 1px solid #EBEBEB;
      @include center;
      height: 2.375em;
      text-transform: uppercase;
      font-weight: 700;
      color: $primary;
      &:hover{
        background-color: $primary;
        border-color: $primary;
        color: #fff;
        path{
          fill: #fff;
        }
      }
      span{
        font-size: 0.75em;
        line-height: 130%;
        letter-spacing: 0.0075em;
      }
      svg{
        width: .3125em;
        height: auto;
        margin-left: .375em;
      }
    }
  }

  .order-info{
    display: block;
    &__nav{
      display: flex;
      margin: .625em 0 0;
    }
  }
}
