.change-password{
  &__grid{
    padding: 1.875em 2.5em 3.125em;
    border: 1px solid #F0F0F0;
    margin-top: 2.25em;
    @include grid100gap(1.875em)
  }
  .fg{
    max-width: 25em;
  }
  .login-form__nav{
    justify-content: unset;
  }
}


@media screen and (max-width: 992px){
  .change-password{
    &__grid{
      margin-top: 1.5em;
      padding: 1.56em;
      max-width: 26.25em;
      gap: 1.56em;
    }
  }
}


@media screen and (max-width: 576px){
  .change-password{
    &__grid{
      margin-top: 1.5em;
      padding: 0;
      max-width: 100%;
      border: none;
    }
  }
}
