.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &__bg {
        background: rgba(0, 0, 0, 0.95);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 39.6875em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        padding: 2.5em 3.125em;
        display: inline-block;
    }

    .close-btn {
        position: absolute;
        top: 1.25em;
        right: 1.25em;
        width: .75em;
        background-color: transparent;
        @include center;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover {
            path {
                fill: $primary;
            }
        }
    }
    &__title{
        text-align: center;
    }
}


.auth-modal{
    &__nav{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .47em));
        gap: .94em;
        margin-top: 2.25em;
        .m-btn{
            height: 3.75em;
            padding: 0;
            span{
                font-size: 1.0625em;
            }
            svg{
                width: 1.1875em;
                margin: 0 .625em 0 0;
            }
        }
    }
}

.address-modal{
    @media screen and (min-width: 993px){
        .modal__dialog{
            width: 58.75em;
        }
    }
}

.address-form{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.875em 2.5em;
    ._full{
        grid-column: 1/-1;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            width: 38.375em;
            padding: 1.5em 2.5em;
        }
    }

    .auth-modal{
        &__nav{
            margin-top: 1.375em;
        }
    }

    .address-form{
        margin-top: 1.25em;
        grid-template-columns: repeat(2,calc(50% - .78em));
        gap: 1.56em;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            padding: 1.5em 1.75em;
        }
    }

    .auth-modal{
        &__nav{
            grid-template-columns: 100%;
        }
    }

    .address-form{
        grid-template-columns: 100%;
    }
}
