.contacts-quote2{
  .contacts-quote__text{
    width: 42.625em;
  }
  .text30{
    line-height: 130%;
  }
}

.services-collection{
  padding: 6em 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
    gap: 2.5em;
  }
}

.service-card{
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    @include imgCover(87.5%);
  }
  &__content{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
  &__title{
    min-height: 4.125em;
    display: flex;
    align-items: center;
    .text24{
      line-height: 137%;
    }
  }
  &__desc{
    color: #565656;
    font-weight: 300;
    font-family: $font2;
    @include grid100gap(.5em);
    p{
      font-size: 0.9375em;
      line-height: 167%;
    }
  }
  .blog-card__nav{
    padding-top: .625em;
  }
}

.services-top{
  padding: 4em 0 5.25em;
  &__inner{
    max-width: 88em;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 40.3125em calc(100% - 54.8125em);
    justify-content: space-between;
    align-items: flex-end;
    gap: .625em 2.5em;
  }
  &__title{
    line-height: 125%;
    letter-spacing: -0.03em;
  }
  &__desc{
    max-width: 32em;
  }
  .text18{
    line-height: 161%;
  }
}

.services-info{
  border-top: 1px solid #EDEDED;
  &__inner{
    max-width: 88em;
    margin: 0 auto;
    display: grid;
    grid-template-columns: calc(100% - 41em) 41em;
  }
  &__items{
    padding: 5.25em 5em 4.87em 0;
    border-right: 1px solid #EDEDED;
  }
  &__item{
    max-width: 38.5625em;
    display: flex;
    align-items: flex-start;
    @include lastM(0 0 3.31em);
    img{
      width: 4em;
      flex-shrink: 0;
      margin-right: 2.25em;
    }
    &-title{
      font-size: .9375em;
      line-height: 167%;
      color: #565656;
      font-family: $font2;
      font-weight: 300;
    }
  }
  &__text{
    padding: 5em 0 4.5em 7.8em;
  }
  &__title{
    max-width: 13em;
  }
  &__desc{
    margin-top: .75em;
    p{
      font-size: 1.125em;
      line-height: 161%;
      @include lastM(0 0 .375em);
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-quote2{
    padding-top: 3.875em;
    padding-bottom: 3.875em;
    .contacts-quote__text{
      width: 26.5em;
    }
  }

  .services-collection{
    padding: 2.125em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 2.375em / 2));
      gap: 1.25em 2.375em;
    }
  }

  .service-card{
    &__title{
      min-height: unset;
      .text24{
        font-size: 1.3125em;
        line-height: 138%;
      }
    }
  }

  .services-top{
    padding: 2.875em 0 2.19em;
    &__inner{
      grid-template-columns: 20.625em calc(100% - 23.125em);
    }
    &__title{
      line-height: 151%;
    }
    .text18{
      font-size: 1em;
      line-height: 156%;
    }
  }

  .page-top{
    &._services{
      .page-top{
        &__link {
          @media screen and (min-width: 577px){
            &:before{
              left: 0;
            }
            &--active:before{
              width: 100%;
            }
            &:not(:last-child) {
              margin-right: 1.94em;
              &:after {
                right: -.94em;
              }
            }
          }
        }
      }
    }
  }

  .services-info{
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      padding: 1.875em 1.5625em 2.19em;
      border-right: none;
      border-bottom: 1px solid #EDEDED;
    }
    &__item{
      max-width: 100%;
      @include lastM(0 0 1.875em);
      img{
        width: 2.875em;
        margin-right: 1.25em;
      }
    }
    &__text{
      padding: 1.875em 1.5625em 2.5em;
    }
    &__title{
      line-height: 122%;
      max-width: 15em;
    }
    &__desc{
      margin-top: .94em;
      p{
        font-size: 1em;
        line-height: 156%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts-quote2{
    padding-top: 5.125em;
    padding-bottom: 5.125em;
  }

  .services-collection{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .services-top{
    padding: 0 0 1.875em;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .services-info{
    &__items{
      padding-left: .9375em;
      padding-right: .9375em;
    }
    &__item{
      @include lastM(0 0 1.25em);
    }
  }
}
