.materials,
.android,
.planner,
.service-contacts,
.edu-contacts{
  &__inner {
    max-width: 100%;
    width: 91.5em;
    margin: 0 auto;
    display: grid;
    align-items: center;
  }
}

.planner{
  padding: 2.75em 0;
  &__inner{
    grid-template-columns: 40.3125em calc(100% - 40.3125em);
  }
  .text56{
    letter-spacing: -.03em;
    line-height: 125%;
  }
  .text2{
    margin-top: 1.75em;
    max-width: 32em;
  }
  &__nav{
    display: flex;
    margin-top: 1.94em;
  }
  &__download{
    margin-left: 1.19em;
    svg{
      width: 1.25em;
    }
  }
  &__img{
    img{
      pointer-events: none;
      width: calc(100% + 4em);
      margin-left: -4em;
      display: block;
      filter: drop-shadow(1px 10px 21px rgba(0, 0, 0, 0.10))
              drop-shadow(5px 39px 39px rgba(0, 0, 0, 0.09))
              drop-shadow(11px 87px 52px rgba(0, 0, 0, 0.05))
              drop-shadow(20px 154px 62px rgba(0, 0, 0, 0.01));
    }
  }
}

.text2{
  li,
  p{
    font-size: 1.125em;
    line-height: 161%;
  }
  p{
    @include lastM(0 0 .375em);
  }
  li{
    @include lastM(0);
    padding-left: 1.5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 1.5em;
      text-align: center;
      top: 0;
      left: 0;
    }
  }
}

.android{
  &__inner{
    grid-template-columns: calc(100% - 48.9375em) 48.9375em;
  }
  &__img{
    img{
      width: calc(100% + 15em);
      display: block;
      margin-left: -15em;
    }
  }
  .text56{
    line-height: 125%;
  }
  .text2{
    margin-top: 1.94em;
  }
  &__store{
    margin-top: 1.94em;
    display: flex;
    align-items: center;
    &-item{
      height: 3.33em;
      img{
        height: 100%;
        display: block;
      }
    }
  }
}

.service-contacts{
  padding: 3.125em 0;
  border-top: 1px solid #EDEDED;
  &__inner{
    width: 96em;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__group{
    max-width: 20em;
  }
  &__title{
    font-size: 0.9375em;
    line-height: 327%;
    color: #AEAEAE;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__item{
    font-size: 1.1875em;
    line-height: 147%;
    font-weight: 500;
    ._underline{
      text-decoration: underline;
    }
  }
  &__text{
    margin-top: .8em;
    font-size: 0.9375em;
    line-height: 160%;
    color: #757575;
    a{
      text-decoration: underline;
      color: $black;
    }
  }
  &__info{
    margin-top: .375em;
    font-size: 0.9375em;
    line-height: 160%;
    font-weight: 500;
  }
  &__nav{
    @include grid100gap(1.5em);
    padding-top: 1.5em;
    width: 22.4375em;
    .m-btn{
      padding: 0 1em;
      height: 3.75em;
      span{
        font-size: 1.0625em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .planner{
    &__inner{
      align-items: flex-start;
      grid-template-columns: 21.9375em calc(100% - 21.9375em - .625em);
      gap: .625em;
    }
    &__img{
      img{
        width: 100%;
        margin: 0;
      }
    }
    .text2{
      margin-top: .625em;
    }
    &__nav{
      @include grid100gap(.94em);
      .m-btn{
        width: 100%;
      }
    }
    &__download{
      margin: 0;
    }
  }

  .text2{
    li,
    p{
      font-size: 1em;
      line-height: 156%;
    }
  }

  .android{
    padding: 1.5625em 0 2.54em;
    &__inner{
      grid-template-columns: 21.9375em calc(100% - 21.9375em);
      gap: 0;
      align-items: flex-start;
    }
    .text56{
      line-height: 151%;
    }
    .text2{
      margin-top: .625em;
    }
    &__store{
      margin-top: .625em;
    }
    &__img{
      img{
        width: 100%;
        margin: 0;
      }
    }
  }

  .service-contacts{
    padding: 2.5em 0;
    &__nav,
    &__inner{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .47em));
      gap: 1.56em .94em;
    }
    &__group{
      max-width: 100%;
    }
    &__nav{
      width: 100%;
      grid-column: 1/-1;
      padding: 0;
    }
    &__items{
      margin-top: -.44em;
    }
  }
}


@media screen and (max-width: 576px){
  .planner{
    padding: 1.25em 0 1.5625em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
      order: -1;
    }
  }

  .android{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .service-contacts{
    &__inner{
      grid-template-columns: 100%;
    }
    &__nav{
      grid-template-columns: 100%;
      gap: .94em;
    }
  }
}
