.profile-edit{
  border: 1px solid #E9EAEE;
  padding: 3.125em;
  @include grid100gap(3.125em);
  &__group{
    @include grid100gap(1.75em);
  }
  .fg{
    max-width: 25em;
  }
  &__nav{
    display: flex;
    .m-btn{
      min-width: 11.3125em;
      @include lastM(0 1.25em 0 0);
    }
  }
  &._type2{
    .fg{
      max-width: 100%;
    }
  }
  &__status{
    @include center;
    color: #24A236;
    font-weight: 500;
    height: 3.75em;
    span{
      font-size: .9375em;
      line-height: 150%;
    }
    svg{
      width: 1.0625em;
      height: auto;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}

.profile-address{
  &__nav{
    display: flex;
    .m-btn{
      height: 3.1875em;
      padding: 0 1.75em;
      span{
        font-size: 1.0625em;
      }
    }
  }
}

.address-card{
  border: .625em solid #F9F9F9;
  overflow: hidden;
  padding: 1em 1.5625em;
  @include grid100gap(2.19em);
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__name{
    color: #121214;
  }
  &__badge{
    height: 3.0625em;
    padding: 0 2.31em;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 700;
    border-radius: 0;
    pointer-events: none;
    background: #F9F9F9;
    color: #303030;
    margin: -1em -1.5625em 0 0;
    span{
      font-size: .75em;
    }
  }
  &__center{
    display: flex;
    justify-content: space-between;
    color: #3B3B3E;
    .text15{
      line-height: 167%;
      letter-spacing: unset;
    }
    .text14{
      color: #87878D;
      line-height: 178%;
    }
    @media screen and (min-width: 993px){
      &._type2{
        max-width: 42em;
      }
    }
  }
  &__address{
    width: 17.625em;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 500;
    height: 1.4375em;
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
      ._fill{
        fill: $primary;
        stroke: none;
      }
    }
    &:not(:last-child){
      margin-right: 1.75em;
    }
    svg{
      height: 1em;
      width: auto;
      margin-right: .56em;
      flex-shrink: 0;
    }
    span{
      font-size: 0.875em;
      line-height: 164%;
    }
  }
  &__new{
    padding: .875em 1.875em 1.875em;
    display: flex;
  }
  &__add{
    display: flex;
    align-items: center;
    color: $primary;
    &:hover{
      color: $black;
    }
    &-icon{
      background: #F9F9F9;
      width: 3.875em;
      height: 3.875em;
      border-radius: 50%;
      flex-shrink: 0;
      @include center;
      margin-right: 1.06em;
      svg{
        width: 1em;
        height: auto;
      }
    }
    .text16{

    }
  }
}


@media screen and (max-width: 992px){
  .profile-edit{
    padding: 1.56em;
    gap: 1.875em;
    margin-top: 2.19em;
    &__group{
      gap: .9375em;
    }
    .text30{
      font-size: 1.6875em;
      line-height: 100%;
    }
    .fg{
      max-width: 100%;
    }
    &__nav{
      display: block;
      .m-btn{
        width: 100%;
        @include lastM(0 0 .9375em);
        &._small{
          height: 3.8125em;
        }
      }
    }
    &__status{
      margin: 0;
    }
  }

  .profile-address{
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .address-card{
    padding-left: .625em;
    padding-right: .625em;
    gap: 1.56em;
    &__nav{
      justify-content: center;
    }
    &__address{
      width: 13.8125em;
    }
  }
}


@media screen and (max-width: 576px){
  .profile-edit{
    margin-top: 1.56em;
    padding: 1.56em 1.25em;
    .m-check{
      label{
        padding-top: 0;
      }
    }
  }

  .address-card{
    padding: .5em;
    &__center{
      @include grid100gap(.875em);
    }
    &__address{
      width: 100%;
    }
    &__top{
      flex-direction: column;
      align-items: center;
    }
    &__badge{
      margin: -.5em 0 .625em;
      order: -1;
    }
  }
}
