.materials{
  padding: 6.38em 0 3.81em;
  &._design{
    @media screen and (min-width: 993px){
      .materials__inner{
        grid-template-columns: calc(100% - 46.4375em) 46.4375em;
      }
      .materials__img{
        width: 46.4375em;
      }
      .materials__content{
        max-width: 43.8125em;
      }
    }
  }
  &__inner{
    align-items: flex-start;
    grid-template-columns: 49em calc(100% - 49em);
    position: relative;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 49em);
    img{
      width: 100%;
      display: block;
    }
  }
  .text38{
    line-height: 142%;
    letter-spacing: -.03em;
  }
  &__subtitle,
  .text2{
    margin-top: 1.94em;
  }
  &__nav{
    margin-top: 1.75em;
    display: flex;
    .m-btn{
      @include lastM(0 1.19em 0 0);
    }
  }
}

.materials-detail{
  padding: 3.81em 0 6.94em;
  background: #FBFBFB;
  &__inner{
    max-width: 100%;
    width: 91.5em;
    margin: 0 auto;
  }
  .text56{
    max-width: 12em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2.3em));
    gap: 4.6em;
    margin-top: 3.625em;
  }
}

.material-item{
  display: flex;
  color: $darkGray;
  &__icon{
    width: 4.9375em;
    height: 4.9375em;
    flex-shrink: 0;
    margin-right: 1.875em;
    background-color: $black;
    @include center;
    img{
      max-width: 2.25em;
      max-height: 3.375em;
    }
  }
  .text17{
    line-height: 159%;
  }
}


@media screen and (max-width: 992px){
  .materials{
    padding: 2.81em 0 2.5em;
    &__inner{
      display: block;
    }
    &__img{
      width: 19.25em;
      position: static;
      float: right;
      margin: 0 0 .625em .625em;
    }
    .text2{
      margin-top: .625em;
    }
    &__subtitle{
      margin-top: .625em;
      text-align: center;
    }
    &__nav{
      margin-top: .625em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .47em));
      gap: .94em;
      .m-btn{
        margin: 0 !important;
      }
    }
  }

  .materials-detail{
    padding: 1.875em 0 8.19em;
    .text56{
      max-width: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.5em;
    }
  }

  .material-item{
    &__icon{
      margin-right: .94em;
      width: 3.625em;
      height: 3.625em;
    }
    .text17{
      font-size: 1em;
      line-height: 162%;
    }
  }
}


@media screen and (max-width: 576px){
  .materials{
    padding-top: 2.5em;
    &__img{
      float: unset;
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
    }
    &__nav{
      grid-template-columns: 100%;
    }
  }

  .materials-detail{
    padding: 1.875em 0 12.44em;
    &__items{
      grid-template-columns: 100%;
    }
  }
}
