.edu-planner{
  @media screen and (min-width: 993px){
    .planner__inner{
      grid-template-columns: 52.625em calc(100% - 52.625em);
    }
    .planner__img{
      img{
        width: calc(100% + 15em);
        margin-left: -15em;
      }
    }
    .text2{
      max-width: 48.75em;
    }
  }
}

.edu-program{
  margin-top: 3em;
  padding: 3.81em 0 5.44em;
  background: #FBFBFB;
  .text56{
    max-width: 12em;
  }
  &__items{
    display: flex;
    justify-content: space-between;
    margin-top: 3.75em;
  }
  &__item{
    max-width: 16.75em;
    &-icon{
      height: 3.1875em;
      display: flex;
      align-items: center;
      margin-bottom: 1.375em;
      img{
        max-height: 100%;
        max-width: 3.6875em;
      }
    }
    &-text{
      color: $darkGray;
      .text17{
        line-height: 158%;
      }
    }
  }
}

.edu-contacts{
  padding: 6.81em 0;
  &__inner{
    grid-template-columns: calc(100% - 45.0625em - 4.625em) 45.0625em;
    justify-content: space-between;
    align-items: unset;
  }
  &__groups{
    display: flex;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    margin-top: 5.625em;
  }
}

.offer-form{
  padding: 2.625em 2.875em;
  background: #FFF;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.06), 0px 10px 22px 0px rgba(0, 0, 0, 0.06), 0px 40px 40px 0px rgba(0, 0, 0, 0.05), 0px 91px 55px 0px rgba(0, 0, 0, 0.03), 0px 162px 65px 0px rgba(0, 0, 0, 0.01), 0px 253px 71px 0px rgba(0, 0, 0, 0.00);
  margin-top: -10em;
  .file-input2{
    &__inner{
      height: 3.75em;
      background-color: transparent;
    }
  }
  .opinion-form__grid{
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 2.5em 1.5em;
  }
}


@media screen and (max-width: 992px){
  .edu-planner{
    @media screen and (min-width: 577px){
      .planner__inner{
        grid-template-columns: calc(100% - 16.5em - .875em) 16.5em;
        gap: .875em;
      }
    }
  }

  .edu-program{
    padding: 1.875em 0 6.19em;
    text-align: center;
    .text56{
      max-width: 100%;
    }
    &__items{
      flex-wrap: wrap;
      justify-content: center;
      margin-top: 1.5em;
    }
    &__item{
      max-width: unset;
      width: 18.125em;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 1.875em .9375em 0;
      &-icon{
        margin-bottom: .94em;
      }
      &-text{
        .text17{
          font-size: 1em;
          line-height: 137%;
        }
      }
    }
  }

  .edu-contacts {
    padding: 0 0 1.75em;
    &__inner {
      grid-template-columns: 100%;
      align-items: unset;
      gap: 2.5em;
    }
    &__sidebar{
      order: -1;
    }
    .service-contacts__group{
      max-width: 20.4375em;
    }
    &__nav{
      margin-top: 2em;
    }
  }

  .offer-form{
    padding: 1.25em;
    margin-top: -4em;
    .opinion-form__grid{
      gap: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .edu-program{
    .text56{
      text-align: left;
    }
    &__items{
      margin-top: -.125em;
    }
    &__item{
      width: 16.75em;
    }
  }

  .edu-contacts{
    &__groups{
      @include grid100gap(2.5em);
    }
  }

  .offer-form{
    .text22-tablet{
      line-height: 145%;
    }
    .opinion-form__grid{
      grid-template-columns: 100%;
    }
  }
}
