.cart{
  padding: 4.38em 0 1.875em;
  &:last-child{
    padding-bottom: 7.5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 36.6875em) 34.1875em;
    gap: 1.875em 2.5em;
  }
}

.cart-table{
  &__grid{
    display: grid;
    grid-template-columns: 1.125em calc(100% - 46.375em) 7.5em 6em 11.25em 7em 6.125em;
    gap: 1.25em;
  }
  &__head{
    border-bottom: 1px solid #EBEBEB;
    &-col{
      min-height: 2.75em;
      display: flex;
      align-items: center;
      letter-spacing: 0.00875em;
    }
    .text14{
      text-transform: uppercase;
      font-weight: 700;
    }
  }
}

.cart-row{
  border-bottom: 1px solid #F6F6F6;
  align-items: center;
  padding: 1.25em 0;
  &__remove{
    width: 1.25em;
    height: auto;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 6.25em calc(100% - 7.5em);
    gap: 1.25em;
    align-items: center;
  }
  &__img{
    display: block;
    @include imgCover(88%);
  }
  &__title{
    font-size: 1em;
    line-height: 115%;
    font-weight: 500;
    color: #52565A;
  }
  &__colors{
    margin-top: .56em;
    @include grid100gap(.56em);
  }
  &__color{
    display: flex;
    align-items: center;
    color: #575757;
    &-title{
      font-size: .875em;
      line-height: 143%;
    }
    &-bg{
      flex-shrink: 0;
      width: 0.9375em;
      height: 0.9375em;
      margin-right: .625em;
    }
  }
}

.cartcalc{
  width: 5.9375em;
  display: grid;
  grid-template-columns: 1.625em calc(100% - 1.625em * 2) 1.625em;
  border: 1px solid #ECEFF4;
  button,
  input{
    height: 2.8125em;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    border-left: 1px solid #ECEFF4;
    border-right: 1px solid #ECEFF4;
    color: #323638;
    font-size: .875em;
    text-align: center;
  }
  svg{
    width: .5em;
    height: auto;
  }
  button{
    @include center;
    &:hover{
      background-color: rgba($primary,.2);
      path{
        fill: $primary;
      }
    }
  }
}

.cart-order{
  position: sticky;
  top: 1em;
  padding: 2.5em 3.125em;
  background: #FBFBFB;
  @include grid100gap(1.875em);
  &__min2,
  &__min{
    padding: .625em;
    @include center;
    border: 1px dashed $primary;
    color: $primary;
    font-weight: 500;
    &-text{
      font-size: 1.125em;
      line-height: 122%;
      &._black{
        color: $black;
        span{
          color: $primary;
        }
      }
    }
    img{
      width: 1em;
      margin-right: .625em;
    }
  }
  &__min2{
    text-align: center;
    border-color: #727272;
  }
  &__bottom{
    padding-top: .94em;
    @include grid100gap(1.25em);
    &._pt0{
      padding-top: 0;
    }
  }
  &__btn{
    height: 3.75em;
    span{
      font-size: 1.125em;
    }
  }
  &__sales-btn{
    height: 2.375em;
    span{
      font-size: 1em;
      text-decoration: underline;
    }
  }
  &__comment{
    textarea{
      padding: .625em .94em;
      border: 1px solid #656565;
      font-size: 1em;
      height: 4.25em;
      resize: none;
      &::placeholder{
        color: #9B9B9B;
      }
    }
  }
  .m-check{
    label{
      padding-top: 0;
      span{
        font-size: .8125em;
        line-height: 140%;
      }
    }
  }
  .m-radio{
    label{
      min-height: 1.5em;
      padding-left: 2.125em;
      display: flex;
      align-items: center;
      &:after,
      &:before{
        width: 1.5em;
        height: 1.5em;
        top: 50%;
        transform: translateY(-50%);
      }
      &:after{
        background-image: url(../img/radio3.svg);
      }
      &:before{
        border-color: $black;
      }
    }
  }
  &__radios{
    @include grid100gap(1.125em);
  }
}

.order-products{
  @include grid100gap(.9375em);
  &__items{
    @include grid100gap(.9375em);
    ._sum{
      .order-products__title,
      .order-products__desc{
        font-size: 1.5em;
      }
    }
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 1.75em;
  }
  &__title{
    font-size: 1.125em;
    font-weight: 500;
  }
  &__desc{
    font-size: 1.0625em;
    line-height: 118%;
    color: $darkGray;
  }
  ._red{
    color: $primary;
  }

}

.cart-promos{
  @media screen and (min-width: 993px){
    .swiper-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.75em;
    }
  }
  .promos-slider{
    position: relative;
  }
}

.cart-promos2{
  padding: 1.875em 0 7.5em;
  @media screen and (min-width: 993px){
    .m-table td:nth-child(2),
    .m-table th:nth-child(2){
      width: 54em;
    }
  }
  .promos-table{
    margin: 0;
  }
}

.cart-success{
  padding: 4.37em 0 6.5em;
  &__inner{
    width: 52.5em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__top{
    padding: 1.25em;
    border: .625em solid #F9F9F9;
    text-align: center;
    @include grid100gap(.25em);
  }
}

.cart-info{
  margin-top: 2.875em;
  display: flex;
  justify-content: space-between;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child){
      @media screen and (min-width: 993px){
        flex-grow: 1;
        &:after{
          content: '';
          height: 4.625em;
          width: 1px;
          border-right: 1px dashed #D7D7D7;
          min-height: 100%;
          flex-shrink: 0;
          margin: 0 auto;
        }
      }
    }
    &-inner{
      @include grid100gap(.44em);
    }
  }
  &__title{
    font-size: .875em;
    line-height: 129%;
    color: #4C4C4C;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 144%;
    font-weight: 500;
  }
}

.cart-detail{
  margin-top: 4em;
  &__title{
    text-align: center;
  }
  &__table{
    margin-top: 2.25em;
    th,td{
      &:nth-child(2){
        width: 4em;
      }
    }
    th{
      padding: 0;
      &:nth-child(2){
        text-align: left;
      }
    }
    td{
      &:nth-child(2){
        text-align: right;
      }
    }
  }
  .order-products__items{
    margin-top: 1.44em;
    gap: .5em;
  }
}

.product-row{
  background-color: transparent !important;
  td{
    padding: 1.25em 0;
  }
  &__top{
    display: flex;
    align-items: center;
    font-weight: 500;
  }
  &__title{
    line-height: 181%;
    color: #52565A;
  }
  &__num{
    color: #ADADAD;
    margin-left: .625em;
  }
  &__item{
    margin-top: .19em;
    span{
      color: #52565A;
    }
  }
}

.cart-info2{
  margin-top: 2em;
  padding-top: 1.44em;
  border-top: 1px solid #B1B1B1;
  @include grid100gap(1.875em);
  &__item{
    @include grid100gap(.75em);
  }
  &__title{
    font-weight: 500;
  }
  &__desc{
    line-height: 153%;
    color: #5C5C5C;
  }
}

.promo-code{
  border: 1px dashed #D2D2D2;
  background: #FFF;
  padding: .31em;
  display: flex;
  input{
    width: 11em;
    font-size: 1em;
    padding: .625em;
    flex-grow: 1;
    border: none;
    height: 3.5em;
    &::placeholder{
      color: #A6A6A6;
    }
    &._error{
      color: $primary;
    }
  }
  &__error,
  &__success,
  &__btn{
    height: 3.5em;
    padding: 0 1.25em;
    white-space: nowrap;
    font-weight: 500;
    @include center;
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__btn{
    background: #FBFBFB;
    color: $black;
    &:hover{
      background: $black;
      color: #fff;
    }
  }
  &__success{
    background: #F6FCF6;
    svg{
      width: 1.125em;
      height: auto;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__error{
    color: $primary;
    background: #F9F3F4;
  }
}

.cart-delivery{
  padding-bottom: .5em;
  &__items{
    @include grid100gap(1.125em);
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__select{
    margin-top: 1em;
    .nice-select{
      height: 2.5625em;
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding: 1.875em 0;
    &:last-child{
      padding-bottom: 5em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .cart-table{
    &__head{
      display: none;
    }
    &__rows{
      @include grid100gap(.94em);
    }
  }

  .cart-row{
    grid-template-columns: 1.125em calc(100% - 22.625em) 8em 5em 6em;
    gap: .625em;
    padding: .625em 0;
    ._hide-tablet{
      display: none;
    }
    ._all-price{
      order: 2;
    }
    &__info{
      grid-template-columns: 5.5625em calc(100% - 5.5625em - .625em);
      gap: .625em;
    }
    &__title{
      font-size: .875em;
    }
    &__colors{
      margin-top: .12em;
      gap: .19em;
    }
  }

  .cart-order{
    padding: 1.25em;
    gap: 1.56em;
    &__min{
      &-text{
        font-size: 1em;
        line-height: 125%;
      }
      img{
        margin-right: .25em;
      }
    }
  }

  .cart-promos2{
    padding: 1.5em 0 5em;
  }

  .cart-success{
    padding: 1.875em 0 5em;
  }

  .cart-info{
    margin-top: 1.875em;
  }

  .cart-detail{
    margin-top: 2.5em;
    &__table{
      margin-top: 1.25em;
      thead{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart-row{
    padding: 0;
    border-bottom: none;
    grid-template-columns: 1.125em calc(100% - 1.125em - .9375em);
    gap: .625em .9375em;
    position: relative;
    &__count{
      display: flex;
      justify-content: flex-end;
      padding: .625em .9375em;
      background: #FAFAFA;
      grid-column: 1/-1;
    }
    &__price{
      position: absolute;
      left: .9375em;
      bottom: 2em;
    }
    &__weight{
      position: absolute;
      left: .9375em;
      bottom: .625em;
    }
  }

  .cart-info{
    @include grid100gap(.94em);
  }

  .cart-detail{
    &__table{
      margin-top: .55em;
    }
    table,
    tbody{
      display: block;
    }
    td{
      padding: 0;
      display: block;
      border: none;
      width: 100% !important;
      text-align: left !important;
      height: auto;
    }
  }

  .product-row{
    border-bottom: 1px solid #F6F6F6;
    padding: 1.25em 0;
    @include grid100gap(.625em);
  }
}
